import apiClient from './apiClient';

const buildPath = (studentId, resource) =>
  `/students/${studentId}/${resource}/`;

const withFallback = async (promise, fallback) => {
  try {
    const { data } = await promise;
    return data;
  } catch (error) {
    const shouldFallback =
      process.env.NODE_ENV === 'development' &&
      process.env.REACT_APP_ALLOW_STUDENT_MOCKS === 'true';

    if (shouldFallback && fallback) {
      console.warn('Student API unavailable. Using mock data.');
      return fallback;
    }

    throw error;
  }
};

